.event-card {
	border-radius: 10px;
	background-color: #fafdff;
	box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.2);
	min-height: 218px;
	display: grid;
	grid-template-rows: 1fr auto;
	position: relative;
	width: 270px;
	min-height: 253px;
}

.event-card-title {
	background-color: #222;
	color: white;
	text-align: center;
	border-radius: 0 0 10px 10px;
	padding: 5px;
	font-size: medium;
	overflow: hidden;
}

.event-card-title div{
	overflow: hidden;
	text-overflow: ellipsis;
}

.event-card-timer {
	padding: 5px;
}

.event-card-timer div {
	text-align: center;
	margin-bottom: 5px;
}

.delete-button {
	position: absolute;
	right: 0;
	margin: 5px;
	width: 40px;
	height: 40px;
	border-radius: 100%;
	background-color: white;
	cursor: pointer;
	border: 1px solid #d9d9d9;
	transition: border 0.3s ease, color 0.3s ease;
}
.delete-button:hover {
	color: #1890ff;
	border: solid 1px #1890ff;
}


.delete-button:focus {
    outline: none;
}

.delete-button:active {
    background-color: #efefef;
}