@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
body,
html,
#root {
	height: 100%;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
		"Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
		"Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

a {
	color: #1890ff;
	text-decoration: none;
}

.header{
    width: 100%;
    background: #222;
    color: white;
    display: grid;
    place-items: center ;
    padding: 15px 0;
    height: 100px;
}

.title{
    margin-top:0;
    font-size: xx-large;
    text-align: center;
}

.subtitle{
    margin-top:0;
    font-size: 1em;
    text-align: center;
}
.content {
	background-color: #f0f2f5;
}

.data-buttons {
	margin: 20px 0 10px 0;
	display: flex;
	justify-content: center;
}

.data-buttons button {
	margin: 0 10px;
	background-color: white;
	cursor: pointer;
	border: 1px solid #d9d9d9;
	font-size: large;
	padding: 5px 10px;
	border-radius: 5px;
	transition: border 0.3s ease, color 0.3s ease;
}

.data-buttons button:hover {
	color: #1890ff;
	border: solid 1px #1890ff;
}

.data-buttons button:focus {
	outline: none;
}

.data-buttons button:active {
	background-color: #efefef;
}

.event-card {
	border-radius: 10px;
	background-color: #fafdff;
	box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.2);
	min-height: 218px;
	display: grid;
	grid-template-rows: 1fr auto;
	position: relative;
	width: 270px;
	min-height: 253px;
}

.event-card-title {
	background-color: #222;
	color: white;
	text-align: center;
	border-radius: 0 0 10px 10px;
	padding: 5px;
	font-size: medium;
	overflow: hidden;
}

.event-card-title div{
	overflow: hidden;
	text-overflow: ellipsis;
}

.event-card-timer {
	padding: 5px;
}

.event-card-timer div {
	text-align: center;
	margin-bottom: 5px;
}

.delete-button {
	position: absolute;
	right: 0;
	margin: 5px;
	width: 40px;
	height: 40px;
	border-radius: 100%;
	background-color: white;
	cursor: pointer;
	border: 1px solid #d9d9d9;
	transition: border 0.3s ease, color 0.3s ease;
}
.delete-button:hover {
	color: #1890ff;
	border: solid 1px #1890ff;
}


.delete-button:focus {
    outline: none;
}

.delete-button:active {
    background-color: #efefef;
}
.card-holder{
    display: grid;
    grid-gap: 2em;
    gap: 2em;
    justify-content: center;
    grid-template-columns: repeat( auto-fit, minmax(270px, -webkit-max-content) );
    grid-template-columns: repeat( auto-fit, minmax(270px, max-content) );
    padding: 1em 4em;
}
.footer{
    background: #222;
    width: 100%;
    background: #222;
    color: white;
    display: grid;
    place-items: center;
    padding: 15px 0;
}

.app {
	height: 100%;
	margin: 0;
	grid-template-rows: auto 1fr auto;
	display: grid;
}

